body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f5f5f5;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body.active {
  overflow: hidden;
  height: 100vh;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul {
  padding: 0;
}

li {
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
}

@media (max-width: 1281px) {
  .container {
    max-width: 95%;
    margin: 0 auto;
  }
}
@media (min-width: 1920px) {
  .container {
    max-width: 1600px;
    margin: 0 auto;
  }
}
.ymaps-layers-pane {
  filter: grayscale(1);
  -ms-filter: grayscale(1);
  -webkit-filter: grayscale(1);
  -moz-filter: grayscale(1);
  -o-filter: grayscale(1);
}

::-webkit-scrollbar-button {
  width: 6px;
  height: 0px;
}

::-webkit-scrollbar-track {
  background-color: #f5f5f5;
  box-shadow: 0px 0px 3px whitesmoke inset;
}

::-webkit-scrollbar-thumb {
  -webkit-border-radius: 5px;
  border-radius: 5px;
  background-color: #aaa;
  box-shadow: 0px 1px 1px #fff inset;
}

::-webkit-resizer {
  width: 7px;
  height: 0px;
}

::-webkit-scrollbar {
  width: 8px;
}

@font-face {
  font-family: "Neutral Face";
  src: url("../fonts/NeutralFaceRegular.woff2") format("woff2"), url("../fonts/NeutralFaceRegular.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "NeutralFace-Bold";
  src: url("../fonts/NeutralFaceBold.woff2") format("woff2"), url("../fonts/NeutralFaceBold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PTRootUIWebRegular";
  src: url("../fonts/PT-Root-UI_Regular.woff2") format("woff2"), url("../fonts/PT-Root-UI_Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PTRootUIWebBold";
  src: url("../fonts/PT-Root-UI_Bold.woff2") format("woff2"), url("../fonts/PT-Root-UI_Bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
.header {
  width: 100%;
  height: 160px;
  padding-top: 30px;
}

.header__nav {
  opacity: 0;
  position: absolute;
  top: -3000px;
  left: 0;
  width: 100%;
  z-index: 20;
  background: #212121;
  transition: 0.2s;
  overflow-y: scroll;
  overflow-x: hidden;
}

.header__nav-list {
  width: 1056px;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: calc(50vh - 210px);
}
.header__nav-list li {
  width: 270px;
  height: 50px;
  font-family: "Neutral Face";
  font-size: 36px;
  line-height: 106%;
  margin-right: 60px;
  margin-bottom: 70px;
}
.header__nav-list li a {
  display: flex;
  text-decoration: none;
  color: #d0d0d0;
  opacity: 1;
  transition: 0.2s;
}
.header__nav-list li a span {
  margin-top: -8px;
  font-size: 14px;
  margin-right: 24px;
}
.header__nav-list li a:hover {
  color: #fff;
}
.header__nav-list li a:active {
  opacity: 0.5;
}
.header__nav-list li a.active-link {
  color: #fff;
}
.header__nav-list li:last-child {
  width: 334px;
}

.header__nav-active {
  opacity: 1;
  top: 0;
  left: 0;
  position: fixed;
  animation: openBurgerMove 0.5s ease-out;
  height: 100vh;
}

@keyframes openBurgerMove {
  0% {
    top: -2000px;
    left: 0;
    opacity: 0;
  }
  10% {
    top: 0;
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
.header__menu {
  position: fixed;
  z-index: 10;
  width: 100%;
  max-width: inherit;
  margin: 0 auto;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.burger {
  padding-top: 1px;
  width: 47px;
  height: 20px;
  cursor: pointer;
  transition: 1s;
}
.burger__line1 {
  width: 47px;
  height: 4px;
  margin-bottom: 9px;
  opacity: 1;
  transition: 0.2s;
  background-image: url(../img/burger-line2.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.burger__line2 {
  width: 47px;
  height: 4px;
  background-image: url(../img/burger-line2.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  opacity: 1;
  transition: 0.2s;
}
.burger:hover .burger__line1 {
  animation: line1 0.4s ease-in-out;
}
.burger:hover .burger__line2 {
  animation: line2 0.4s ease-in-out;
}
.burger:active .burger__line1 {
  opacity: 0.5;
}
.burger:active .burger__line2 {
  opacity: 0.5;
}

@keyframes line1 {
  70% {
    transform: rotate(5deg);
  }
  100% {
    transform: rotate(-5deg);
  }
}
@keyframes line2 {
  70% {
    transform: rotate(-5deg);
  }
  100% {
    transform: rotate(5deg);
  }
}
.header__nav-close {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 30px;
  margin-bottom: calc(50vh - 210px);
}

.close {
  cursor: pointer;
  transition: 0.2s;
  opacity: 1;
}
.close:hover {
  animation: close-move 0.3s;
}
.close:active {
  opacity: 0.5;
}

@keyframes close-move {
  80% {
    transform: rotate(10deg);
  }
  100% {
    transform: rotate(-10deg);
  }
}
.burger__contacts {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.burger__contacts p {
  font-family: "PTRootUIWebRegular";
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 148%;
  color: #ffffff;
}
.burger__contacts a {
  text-decoration: none;
  font-family: "PTRootUIWebRegular";
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  color: #ffffff;
}

.burger__contacts-img svg {
  width: 72px;
}
.burger__contacts-img svg path {
  transition: 0.3s;
}
.burger__contacts-img svg:hover path {
  opacity: 0.5;
}

.burger__contacts-group {
  display: flex;
  justify-content: space-between;
  width: 450px;
}

.social__list {
  display: flex;
  align-items: center;
}
.social__list li {
  text-decoration: none;
  font-family: "Neutral Face";
  font-style: normal;
  font-weight: bold;
  font-size: 21px;
  line-height: 106%;
  color: #ffffff;
}
.social__list a {
  text-decoration: none;
  font-family: "Neutral Face";
  font-style: normal;
  font-weight: bold;
  font-size: 21px;
  line-height: 106%;
  color: #ffffff;
  transition: 0.2s;
  opacity: 1;
}
.social__list a:hover {
  opacity: 0.7;
}
.social__list a:active {
  opacity: 0.5;
}

.logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 175px;
  height: 100%;
}
.logo img {
  transition: 0.3s;
}
.logo img:hover {
  opacity: 0.5;
}
.logo svg path {
  transition: 0.3s;
}
.logo svg:hover path {
  opacity: 0.5;
}

.logo__lang-list {
  position: relative;
}
.logo__lang-list:hover .en {
  display: block;
}
.logo__lang-list:hover img {
  transform: rotate(180deg);
}

.translator-arrow {
  transition: 0.3s;
  margin-left: 10px;
}

.sub-logo__lang-list {
  position: absolute;
  top: 30px;
}

.ru {
  width: 66px;
  height: 30px;
  display: flex;
  align-items: center;
  font-size: 21px;
  font-family: "PTRootUIWebBold";
  text-transform: uppercase;
  cursor: pointer;
  color: #212121;
}

.en {
  width: 66px;
  height: 30px;
  display: flex;
  align-items: center;
  font-size: 21px;
  font-family: "PTRootUIWebBold";
  text-transform: uppercase;
  cursor: pointer;
  display: none;
}
.en a {
  text-decoration: none;
  color: #212121;
}
.en:hover {
  opacity: 0.7;
}

.header__nav-lang {
  display: flex;
  align-items: center;
  font-size: 21px;
  font-family: "PTRootUIWebBold";
  text-transform: uppercase;
  cursor: pointer;
  color: #fff;
}

